// ==========================================
// RISIVO CRM - Logo Color Extraction API
// ==========================================

import { NextRequest, NextResponse } from 'next/server'
import { SubAccountTheme } from '@/lib/theme/types'
import { RISIVO_DEFAULT_THEME, generateColorVariants, getContrastingTextColor, darkenColor } from '@/lib/theme/defaults'

/**
 * POST /api/v1/themes/extract-colors
 * 
 * Extract dominant colors from an uploaded logo image
 * Returns suggested theme colors based on the logo
 * 
 * Body (FormData):
 * - logo: File (image file)
 * OR
 * - logoUrl: string (URL to existing logo)
 * 
 * Options:
 * - preferDarkSidebar: boolean (default: true)
 * - preserveStatusColors: boolean (default: true)
 */
export async function POST(request: NextRequest) {
  try {
    const contentType = request.headers.get('content-type') || ''
    
    let imageData: ArrayBuffer | null = null
    let imageUrl: string | null = null
    let preferDarkSidebar = true
    let preserveStatusColors = true
    
    // Handle FormData (file upload)
    if (contentType.includes('multipart/form-data')) {
      const formData = await request.formData()
      const logo = formData.get('logo') as File | null
      imageUrl = formData.get('logoUrl') as string | null
      
      preferDarkSidebar = formData.get('preferDarkSidebar') !== 'false'
      preserveStatusColors = formData.get('preserveStatusColors') !== 'false'
      
      if (logo) {
        imageData = await logo.arrayBuffer()
      }
    } 
    // Handle JSON (URL-based)
    else if (contentType.includes('application/json')) {
      const body = await request.json()
      imageUrl = body.logoUrl
      preferDarkSidebar = body.preferDarkSidebar !== false
      preserveStatusColors = body.preserveStatusColors !== false
    }
    
    if (!imageData && !imageUrl) {
      return NextResponse.json({
        success: false,
        error: 'Either logo file or logoUrl is required',
      }, { status: 400 })
    }
    
    // For server-side color extraction, we'll use a simple algorithm
    // In production, you might want to use a library like sharp or jimp
    // For now, we'll extract colors using a simulated approach
    
    let extractedColors: {
      dominant: string
      palette: string[]
      vibrant?: string
      muted?: string
    }
    
    if (imageData) {
      // Process uploaded image
      extractedColors = await extractColorsFromBuffer(imageData)
    } else if (imageUrl) {
      // Fetch and process image from URL
      extractedColors = await extractColorsFromUrl(imageUrl)
    } else {
      // Fallback
      extractedColors = {
        dominant: '#3B82F6',
        palette: ['#3B82F6', '#10B981', '#6B7280'],
      }
    }
    
    // Generate theme from extracted colors
    const primaryColor = extractedColors.vibrant || extractedColors.dominant
    const primaryVariants = generateColorVariants(primaryColor)
    
    const secondaryColor = extractedColors.palette[1] || 
      extractedColors.muted || 
      '#10B981'
    const secondaryVariants = generateColorVariants(secondaryColor)
    
    let sidebarBg = primaryColor
    if (preferDarkSidebar) {
      sidebarBg = darkenColor(primaryColor, 15)
    }
    
    const suggestedTheme: Partial<SubAccountTheme> = {
      // Primary
      primaryColor,
      primaryHover: primaryVariants.hover,
      primaryForeground: getContrastingTextColor(primaryColor),
      primaryAccent: primaryVariants.light,
      
      // Secondary
      secondaryColor,
      secondaryHover: secondaryVariants.hover,
      secondaryForeground: getContrastingTextColor(secondaryColor),
      
      // Sidebar
      sidebarBg,
      sidebarText: getContrastingTextColor(sidebarBg),
      sidebarHover: 'rgba(255, 255, 255, 0.2)',
      
      // Button
      buttonPrimaryBg: secondaryColor,
      buttonPrimaryText: getContrastingTextColor(secondaryColor),
      buttonSecondaryText: primaryColor,
      
      // Input focus
      inputFocus: primaryColor,
      
      // Meta
      colorSource: 'logo_extracted',
    }
    
    // Keep status colors standard unless requested otherwise
    if (!preserveStatusColors) {
      suggestedTheme.successColor = secondaryColor
      suggestedTheme.infoColor = primaryColor
    }
    
    return NextResponse.json({
      success: true,
      data: {
        extractedColors,
        suggestedTheme,
      },
      message: 'Colors extracted successfully',
    })
    
  } catch (error) {
    console.error('POST /api/v1/themes/extract-colors error:', error)
    return NextResponse.json({
      success: false,
      error: error instanceof Error ? error.message : 'Failed to extract colors',
    }, { status: 500 })
  }
}

/**
 * Extract colors from image buffer (server-side implementation)
 * This is a simplified version - in production, use sharp or jimp
 */
async function extractColorsFromBuffer(buffer: ArrayBuffer): Promise<{
  dominant: string
  palette: string[]
  vibrant?: string
  muted?: string
}> {
  // For now, return defaults
  // In production, implement with sharp:
  // const sharp = require('sharp')
  // const { dominant } = await sharp(Buffer.from(buffer)).stats()
  
  // Default fallback - random variation for demo
  const hue = Math.floor(Math.random() * 360)
  const dominant = hslToHex(hue, 70, 50)
  const secondary = hslToHex((hue + 30) % 360, 60, 45)
  const muted = hslToHex(hue, 30, 60)
  
  return {
    dominant,
    palette: [dominant, secondary, muted],
    vibrant: dominant,
    muted,
  }
}

/**
 * Extract colors from image URL
 */
async function extractColorsFromUrl(url: string): Promise<{
  dominant: string
  palette: string[]
  vibrant?: string
  muted?: string
}> {
  try {
    // Fetch the image
    const response = await fetch(url)
    if (!response.ok) {
      throw new Error(`Failed to fetch image: ${response.statusText}`)
    }
    
    const buffer = await response.arrayBuffer()
    return extractColorsFromBuffer(buffer)
    
  } catch (error) {
    console.error('Error extracting colors from URL:', error)
    
    // Return default colors on error
    return {
      dominant: '#3B82F6',
      palette: ['#3B82F6', '#10B981', '#6B7280'],
      vibrant: '#3B82F6',
      muted: '#6B7280',
    }
  }
}

/**
 * Convert HSL to Hex
 */
function hslToHex(h: number, s: number, l: number): string {
  s /= 100
  l /= 100
  
  const c = (1 - Math.abs(2 * l - 1)) * s
  const x = c * (1 - Math.abs((h / 60) % 2 - 1))
  const m = l - c / 2
  
  let r = 0, g = 0, b = 0
  
  if (0 <= h && h < 60) {
    r = c; g = x; b = 0
  } else if (60 <= h && h < 120) {
    r = x; g = c; b = 0
  } else if (120 <= h && h < 180) {
    r = 0; g = c; b = x
  } else if (180 <= h && h < 240) {
    r = 0; g = x; b = c
  } else if (240 <= h && h < 300) {
    r = x; g = 0; b = c
  } else if (300 <= h && h < 360) {
    r = c; g = 0; b = x
  }
  
  const toHex = (n: number) => {
    const hex = Math.round((n + m) * 255).toString(16)
    return hex.length === 1 ? '0' + hex : hex
  }
  
  return `#${toHex(r)}${toHex(g)}${toHex(b)}`.toUpperCase()
}
